import React, { Component } from 'react';
import './Beijing.css';

class Beijing extends Component{
    constructor(props){
        super(props);
        this.state = {
            image: []
        }
    }
    componentDidMount(){
        document.title = 'Beijing | FTL';
    }

    zoomImage(model) {
        var modal = document.getElementById('myModal');
        let modalImg = document.getElementById("myImg");
        modal.style.display = 'block';
        modalImg.src = require('./../../img/Beijing/image' + model + '.JPG');
      }
    
    imgClose = () => {
        var modal = document.getElementById('myModal');
        modal.style.display = 'none';
    }

    imagerender = () => {
        let index = 45;
        let array = [];
        for (var i = 1; i < index; i++){
            array.push(
                <div className='col-lg-3 col-md-4 col-sm-4 col-6'>
                    <div className='sample-show'>
                    <img className='sample-pic' src={require('./../../img/Beijing/image' + i + '.JPG')} alt='asd' onClick={() => this.zoomImage(i)} />
                    </div>
                </div>
            )
        }
        return array;
    }

    render(){
        let {img} = this.imagerender();
        return(
            <div>
                <div className='beijing-hero'></div>
                <div className='container' style={{marginTop: 30}}>
                    <div className='row'>
                        {/* <div className='col-lg-3 col-md-4 col-sm-4 col-6'>
                            <div className='sample-show'>
                            <img className='sample-pic' src={require('./../../img/Beijing/image' + 1 + '.JPG')} alt='asd' onClick={() => this.zoomImage(1)} />
                            </div>
                        </div>
                        <div className='col-lg-3 col-md-4 col-sm-4 col-6'>
                            <div className='sample-show'>
                            <img className='sample-pic' src={require('./../../img/Beijing/image' + 2 + '.JPG')} alt='asd' onClick={() => this.zoomImage(2)} />
                            </div>
                        </div> */}
                        {img}
                    </div>
                </div>

                <div id="myModal" className="modal">
                    <span className="close" onClick={()=>this.imgClose()}>&times;</span>
                    <img className="modal-content" id="myImg" alt='modal' onClick={()=>this.imgClose()}/>
                </div>
            </div>
        );
    }

}

export default Beijing;