import React, { Component } from 'react';
import Menu from './template/Menu';
import SideDrawer from './template/SideDrawer/SideDrawer';
import Backdrop from './components/Backdrop';
import Footer from './template/Footer';
import {TinyButton as ScrollUpButton} from "react-scroll-up-button";
import Home from './route/Home';
import Contact from './route/Contact';
import Photo from './route/Photo';
import Beijing from './route/timeline/Beijing';
import './App.css';
import{
  HashRouter,
  Route, Switch
} from 'react-router-dom';

class App extends Component {
  state = {
    sideDrawerOpen: false
  };

  drawerToggleClickerHandler = () => {
    this.setState((prevState) => {
      return {sideDrawerOpen: !prevState.sideDrawerOpen};
    });
  };

  backdropClickHandler = () =>{
    this.setState({sideDrawerOpen: false});
  };

  render() {
    let backdrop;
    if(this.state.sideDrawerOpen){
      backdrop = <Backdrop click={this.backdropClickHandler}/>;
    }

    return (
          <div style={{height: '100%'}}>
          <Menu drawerClickHandler={this.drawerToggleClickerHandler}/>
          <SideDrawer show={this.state.sideDrawerOpen}/>
          {backdrop}
          <div>
            <HashRouter>
              <Switch>
                <Route exact path='/' component={Home} />
                <Route exact path='/contact' component={Contact} />
                <Route exact path='/photo' component={Photo} />
                <Route exact path='/beijing' component={Beijing} />
              </Switch>
            </HashRouter>
          </div>
          <div>
        <ScrollUpButton />
          <Footer />
      </div>
    </div>
    );
  }
}

export default App;
