import React from 'react';
import resume from './../../img/Tianlu Feng resume 19W.pdf';
import './SideDrawer.css';

const sideDrawer = props => {

    let drawerClasses = 'side-drawer';
    if(props.show){
        drawerClasses = 'side-drawer open';
    }
    return (
    <nav className={drawerClasses}>
        <ul>
            <li><a href='#/'>Home</a></li>
            <li><a href='#/photo'>Photo</a></li>
            <li><a href='https://github.com/ftlll'>Projects</a></li>
            <li><a href={resume}>Resume</a></li>
            <li><a href="javascript:void(0)" onClick={() => window.scrollTo({ top: document.documentElement.offsetHeight, left: 0, behavior: 'smooth' })}>Contact</a></li>
        </ul>
    </nav>
    );
};

export default sideDrawer;