import React, { Component } from 'react';
import ToggleButton from './SideDrawer/ToggleButton';
import './Menu.css';
import resume from './../img/Tianlu Feng resume 19W.pdf';

class Menu extends Component{
    constructor(props){
        super(props);
    }
    componentDidMount() {
        window.addEventListener("scroll", this.resizeHeaderOnScroll);
      }
      resizeHeaderOnScroll() {
        const distanceY = window.pageYOffset || document.documentElement.scrollTop,
          shrinkOn = 200,
          headerEl = document.getElementById("tb"),
          logo = document.getElementById("logo");
    
        if (distanceY > shrinkOn) {
          headerEl.classList.remove("toolbar");
          headerEl.classList.add("smaller");
          logo.classList.remove("logo-big");
          logo.classList.add("logo-small");
        } else {
          headerEl.classList.remove("smaller");
          headerEl.classList.add("toolbar");
          logo.classList.remove("logo-small");
          logo.classList.add("logo-big");
        }
      }

      scrollToBottom = () =>{
        window.scrollTo({ top: document.documentElement.offsetHeight, left: 0, behavior: 'smooth' });
      }
      
      render(){
          return (
            <header className='toolbar' id='tb'>
                <nav className="toolbar-nav">
                    <div className="toolbar-logo"><a href="#/" id='logo'>Tianlu Feng</a></div>
                    <div className="spacer"></div>
                    <ul className="toolbar-nav-items"> 
                        <li><a href='#/'>Home</a></li>
                        <li><a href='#/photo'>Photo</a></li>
                        <li><a href='https://github.com/ftlll'>Projects</a></li>
                        <li><a href={resume}>Resume</a></li>
                        <li><a href="javascript:void(0)" onClick={() => this.scrollToBottom()}>Contact</a></li>
                    </ul>
                    <div className='toggleButton'>
                        <ToggleButton click={this.props.drawerClickHandler}/>
                    </div>
                </nav>
            </header>
          );
      }
}
